<template>
  <el-dialog v-model="editDialogVisible" :title="title" :before-close="handleClose" width="600px" draggable>
    <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        inline
        label-width="80px"
        label-position="right"
        status-icon>
      <el-form-item label="姓名" prop="name" style="width: 48%;">
        <el-input v-model="editForm.name"/>
      </el-form-item>
      <el-form-item label="电话" prop="tel" style="width: 40%;">
        <el-input v-model="editForm.tel"/>
      </el-form-item>
      <el-form-item label="身份证号" prop="idNo" style="width: 100%;">
        <el-input v-model="editForm.idNo"/>
      </el-form-item>
      <el-form-item label="性别" prop="sex" style="width: 48%;">
        <el-switch
            v-model="editForm.sex"
            style="--el-switch-on-color:  #f89898; --el-switch-off-color: #409EFF"
            :active-icon="Female"
            :inactive-icon="Male"
            :active-value="true"
            :inactive-value="false"
            width="50px"
        />
      </el-form-item>
      <el-form-item label="状态" prop="disabled" style="width: 40%;">
        <el-switch
            v-model="editForm.disabled"
            style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
            active-text="启用"
            inactive-text="禁用"
            :active-value="false"
            :inactive-value="true"
            width="50px"
        />
      </el-form-item>
      <el-form-item label="备注" prop="note" style="width: 100%;">
        <el-input v-model="editForm.note" type="textarea" :rows="5"/>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="handleReset(id)">重置</el-button>
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleConfirm">确定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script setup>
import {defineProps, defineEmits, computed, ref, watch, reactive} from 'vue';
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessageBox, ElMessage} from "element-plus";
import {Male, Female} from '@element-plus/icons-vue'

// 定义传入该子组件的参数
const props = defineProps({
  editDialogVisible: {
    type: Boolean,
    required: true,
    default: false
  },
  id: {
    type: Number,
    required: true,
    default: -1
  },
  title: {
    type: String,
    required: true,
    default: ''
  }
});

// 定义组件（自定义）事件
const emit = defineEmits(['update:editDialogVisible', 'handleSubmit']);

// 定义“编辑”对话框的显示状态
const editDialogVisible = computed({
  // 只读！读取时从父组件获取，更新时更新父组件
  get() {
    return props.editDialogVisible;
  },
  set(newValue) {
    emit('update:editDialogVisible', newValue);
  }
});

// 定义表单数据
const editForm = ref({
  id: -1,
  name: '',
  idNo: '',
  sex: false,
  tel: '',
  disabled: false,
  note: '',
});


const validatePerson = async (rule, value, callback) => {
  // console.log(rule.field)
  // console.log(value)

  let url;
  // 基础格式验证通过后，才给 url 赋值。
  if (rule.field == 'idNo') {
    let pattern = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
    if (!pattern.test(value)) {
      callback(new Error('请输入正确的身份证号'));
    } else {
      url = '/api/sys/persons/checkPerson?id=' + editForm.value.id + '&idNo=' + value;
    }
  } else if (rule.field == 'tel') {
    let pattern = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
    if (!pattern.test(value)) {
      callback(new Error('请输入正确的手机号'));
    } else {
      url = '/api/sys/persons/checkPerson?id=' + editForm.value.id + '&tel=' + value;
    }
  }
  // url 有值，才向后台发送验证请求。
  // console.log(url);
  if (url) {
    // console.log('向后台发送请求');
    let res = await AxiosUtil.post(url);
    // console.log(res.data);
    if (res.data.code == 1) {
      callback(new Error(res.data.msg));
    } else {
      callback();
    }
  }
}

// 定义表单验证规则
const editFormRules = reactive({
  name: [
    {required: true, message: '姓名不能为空', trigger: 'blur'},
    {min: 2, max: 16, message: '请输入正确的姓名', trigger: 'change'},
    {min: 2, max: 16, message: '请输入正确的姓名', trigger: 'blur'},
  ],
  idNo: [
    {required: true, message: '身份证号不能为空', trigger: 'blur'},
    {validator: validatePerson, trigger: 'change'},
    {validator: validatePerson, trigger: 'blur'},
  ],
  tel: [
    {required: true, message: '手机号不能为空', trigger: 'blur'},
    {validator: validatePerson, trigger: 'change'},
    {validator: validatePerson, trigger: 'blur'},
  ],
  disabled: [
    {required: true, message: '状态不能为空', trigger: 'blur'},
  ],
  note: [
    {required: false, max: 192, message: '备注信息不可超过192个字符', trigger: 'blur'},
  ],
})

// 定义表单引用
const editFormRef = ref(null);

// 表单重置事件
const handleReset = async (id) => {
  if (id == -1) {
    editForm.value = {
      id: -1,
      name: '',
      idNo: '',
      sex: false,
      tel: '',
      disabled: false,
      note: '',
    };
    console.log('清空表单...');
  } else {
    let res = await AxiosUtil.get('/api/sys/persons/' + id);
    console.log(res.data);
    if (res.data.code == 20010) {
      let person = res.data.person;
      // 更新表格数据
      editForm.value = person;
    } else {
      ElMessage({
        message: res.data.msg,
        type: 'error',
      });
    }
    console.log('从后台填充...');
  }
}

// 定义关闭对话框前的确认操作
const handleClose = (done) => {
  ElMessageBox.confirm('确认在未保存的情况下关闭对话框？')
      .then(() => {
        done()
      })
      .catch(() => {
        // catch error
      })
}

// 定义对话框关闭事件
const handleCancel = () => {
  editDialogVisible.value = false;
}

// 定义确认按钮事件
const handleConfirm = () => {
  // 表单验证
  editFormRef.value.validate(async valid => {
    if (valid) {
      // 表单验证通过
      let url, res;
      if (editForm.value.id == -1) {
        console.log('提交新增...');
        url = '/api/sys/persons';
        res = await AxiosUtil.post(url, editForm.value);
        console.log("新增身份 res.data= ", res.data);
      } else {
        console.log('提交修改...');
        url = '/api/sys/persons';
        res = await AxiosUtil.put(url, editForm.value);
        console.log("修改身份 res.data= ", res.data);
      }
      if (res.data.code == 20020 || res.data.code == 20030) {
        ElMessage({
          message: res.data.msg,
          type: 'success',
        });
        emit('handleSubmit'); // 触发自定义事件，使父组件刷新数据
        handleCancel(); // 关闭对话框
      } else {
        ElMessage({
          message: res.data.msg,
          type: 'error',
        });
      }
    }
  });
}

// 当对话框重新显示时重置表单
watch(editDialogVisible, (newValue) => {
  if (newValue == true) {
    handleReset(props.id);
  }
})

</script>


<script>
export default {
  name: "EditDialog"
}
</script>

<style scoped>

</style>